<script>
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex';
import CartItem from '@/components/CartItem.vue';

export default {
  components: { CartItem },
  data() {
    return {
      loaded: 0,
    };
  },
  filters: {
    numberFormat,
  },
  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice', totalProducts: 'cartTotalItems' }),
    isCartLoading: {
      get() {
        return this.$store.state.isCartLoading;
      },
    },
  },
