<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
    <div class="product__counter form__counter">
        <button type="button" aria-label="Убрать один товар" @click="deleteProduct">
            <svg width="12" height="12" fill="currentColor">
                <use xlink:href="#icon-minus"></use>
            </svg>
        </button>

        <input type="text" v-model.number="currentProductAmount">

        <button type="button" aria-label="Добавить один товар" @click="addProduct">
            <svg width="12" height="12" fill="currentColor">
                <use xlink:href="#icon-plus"></use>
            </svg>
        </button>
    </div>
</template>

<script>
export default {
  props: ['productAmount'],
  data() {
    return {
      currentProductAmount: 1,
    };
  },
  watch: {
    productAmount(value) {
      this.currentProductAmount = value;
    },
  },
  methods: {
    deleteProduct(evt) {
      if (this.productAmount <= 1) {
        evt.stopPropogation();
      } else {
        this.currentProductAmount -= 1;
      }
      this.$emit('update:productAmount', this.currentProductAmount);
    },
    addProduct() {
      this.$emit('update:productAmount', this.currentProductAmount += 1);
    },
  },
};
</script>
